import React, { useState, useEffect } from "react";
import Backdrop from "@material-ui/core/Backdrop";
import { makeStyles } from "@material-ui/core/styles";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import { Button } from "react-bootstrap";
import { createUniqueId } from "../../IFormDesignerLogic";
import axios from "../../../../axios";
import { useTranslation } from "react-i18next";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: 200,
    color: "#fff"
  }
}));

function AdvanceListViewColumnListBackdrop(props) {
  const contexts = GetStateFromData(props.containerId);

  const {
    activePage,
    microFrontendData,
    activeSubForm,
    formState2,
    openCanvasForSubform,
    updateJson,
    setIsTableBackdropOpen,
    showAlertConfirmNotifications
  } = contexts.contexts;

  const { updatedComponentInFormState } = updateJson;
  const classes = useStyles();
  const translationStyles1 =
    window?.depsatkur_configs?.selectedLanguage === "ar" ||
    window?.depsatkur_configs?.selectedLanguage === "ar-SA";
  const { t } = useTranslation();

  const [selectedColumns, setSelectedColumns] = useState([]);
  const [allColumnsInPage, setAllColumnsInPage] = useState([]);
  const [columnWidth, setColumnWidth] = useState([]);
  const [complexFields, setComplexFields] = useState([]);

  const columnDetailsList = [];
  const pageColumnsName = [];
  let tableColumnsName = [];
  let tableColumnsControlId = [];
  let selectedColumnsNameFromPage = [];

  useEffect(() => {
    if (props?.allColumnsList?.length) {
      const alreadyAddedControlAsColumns = props.allColumnsList.filter((item) => item?.style?.src === "page");

      if (alreadyAddedControlAsColumns?.length) {
        const alreadyAddedControlAsColumnsStyle = alreadyAddedControlAsColumns.map((item) => item.style);
        setAllColumnsInPage(alreadyAddedControlAsColumnsStyle);
      }

      let alreadyAddedCompleFields = [];
      for (let i = 0; i < props.allColumnsList.length; i++) {
        const item = props.allColumnsList[i];
        if(item?.style?.complexFieldName) {
          alreadyAddedCompleFields.push(item?.style?.complexFieldName);
        }
      }

      setComplexFields(alreadyAddedCompleFields);

      let currentWidth = 0;
      let isWidthInPercentage = props?.allColumnsList?.[0]?.style?.ColumnWidth?.includes("%");

      if (isWidthInPercentage) {
        props.allColumnsList.forEach((item) => {
          const curColumnWidth = item?.style?.ColumnWidth ? item?.style?.ColumnWidth.replace("%", "") : 0;
          currentWidth += parseFloat(curColumnWidth);
        });
      }

      setColumnWidth(isWidthInPercentage ? currentWidth : "N");
    }
  }, []);

  if (props.data) {
    if (props.data.columnList) {
      props.data.columnList.map((obj) => {
        tableColumnsName.push(obj?.style?.ColumnName);
        tableColumnsControlId.push(obj?.style?.controlFromPageId);
      });
    }

    if (props.data.listviewColumnList) {
      recursiveJsonReader(props.data.listviewColumnList);
    }

    if (props.data.listviewFrameLayoutJson) {
      recursiveJsonReader(props.data.listviewFrameLayoutJson);
    }

    selectedColumnsNameFromPage = pageColumnsName.filter((name) => tableColumnsName.includes(name));
  }

  function recursiveJsonReader(jsonData) {
    if (Array.isArray(jsonData)) {
      jsonData.forEach((item) => {
        if (
          item["elem_type"] === "textbox" ||
          item["elem_type"] === "textarea" ||
          item["elem_type"] === "radio" ||
          item["elem_type"] === "combo" ||
          item["elem_type"] === "checkbox" ||
          item["elem_type"] === "datepick" ||
          item["elem_type"] === "listbox" ||
          item["elem_type"] === "toggle" ||
          item["elem_type"] === "label" ||
          item["elem_type"] === "link" ||
          item["elem_type"] === "picklist"
        ) {
          if (!tableColumnsControlId.includes(item.id)) {
            columnDetailsList.push({
              name: item["style"]["Label"] || item["id"],
              elem_type: item["elem_type"],
              id: item["id"],
              dataclass: item["dataclass"],
			        PickerType:item["style"]["PickerType"]
            });
          }
          pageColumnsName.push( item["id"] || item["style"]["Label"] );
        }
        if (item.children) {
          recursiveJsonReader(item);
        }
      });
    } else {
      if (
        jsonData["elem_type"] === "textbox" ||
        jsonData["elem_type"] === "textarea" ||
        jsonData["elem_type"] === "radio" ||
        jsonData["elem_type"] === "combo" ||
        jsonData["elem_type"] === "checkbox" ||
        jsonData["elem_type"] === "datepick" ||
        jsonData["elem_type"] === "listbox" ||
        jsonData["elem_type"] === "toggle" ||
        jsonData["elem_type"] === "label" ||
        jsonData["elem_type"] === "link" ||
        jsonData["elem_type"] === "picklist"
      ) {
        if (!tableColumnsControlId.includes(jsonData.id)) {
          columnDetailsList.push({
            name: jsonData["style"]["Label"] || jsonData["id"],
            elem_type: jsonData["elem_type"],
            id: jsonData["id"],
            PickerType: jsonData["style"]["PickerType"]
          });
        }
        pageColumnsName.push(jsonData["id"] || jsonData["style"]["Label"] );
      } else {
        recursiveJsonReader(jsonData["children"]);
      }
    }
  }

  function checkForColumnWidth(e) {
    let shouldAdd = true;

    if (columnWidth === "N") return shouldAdd;

    if (e.target.checked) {
      if (100 - columnWidth >= 10) {
        setColumnWidth((prev) => prev + 10);
      } else {
        shouldAdd = false;
      }
    } else {
      setColumnWidth((prev) => prev - 10);
    }

    return shouldAdd;
  }

  function addColumn(e, selColumn) {
    if(complexFields.includes(selColumn.dataclass)) {
      showAlertConfirmNotifications({
        open: true,
        showConfirm: "showNotification",
        alertType: "error",
        alertContentKey: "A column is already mapped for this control"
      });
      e.target.checked = false;
      return;
    }

    const shouldAdd = checkForColumnWidth(e);
    if (!shouldAdd) {
      showAlertConfirmNotifications({
        open: true,
        showConfirm: "showNotification",
        alertType: "error",
        alertContentKey: "Total column width Left should be greater than 10%."
      });
      e.target.checked = false;
      return;
    }

    const updatedColumns = selectedColumns.filter((item) => item?.ColumnName !== selColumn.name);
    const oldLength = updatedColumns.length;
    const allColumnsupdatedColumns = allColumnsInPage.filter((item) => item?.ColumnName !== selColumn.name);

    setSelectedColumns(updatedColumns);
    setAllColumnsInPage(allColumnsupdatedColumns);

    if (oldLength === selectedColumns.length) {
      const newColJson = {
        FormId: `${microFrontendData?.formDefId || activePage?.formId || activeSubForm?.fragmentId}`,
        frameIndex: props.data.frameIndex,
        elementIndex: props.data.elementIndex,
        elem_type: "column",
        isAdvanceListview: false,
        ColumnName: selColumn.name,
        VisibleOnTable: "true",
        ShowTotal: "false",
        SortingType: "false",
        EnableSearch: "true",
        AllowDuplicate: "true",
        AssociatedField: "",
        ColumnWidth: "10%",
        ColumnType: selColumn.elem_type === "datepick" ? selColumn.PickerType === "2" ? "datetimepick" : "datepick" : selColumn.elem_type,
        ColumnTooltip: "",
        MaskingPattern: "",
        SaveEncrypted: "",
        AllowAlphabets: "true",
        AllowNull: "true",
        AllowNumbers: "true",
        AllowSpaces: "true",
        AllowSpecialChars: "true",
        columnCharacterSet: "",
        columnMinValue: "",
        columnMaxValue: "",
        columnMaxLength: "",
        "column,CustomPattern": "",
        ColumnHeaderFontWeight: "",
        ColumnHeaderColor: "",
        ColumnHeaderAlignment: "",
        ColumnValuesFontWeight: "",
        ColumnValuesColor: "",
        ColumnValuesAlignment: "",
        DisplayLabelValue: "Display Label",
        AddRowButtonLabel: "true",
        isModify: "false",
        ColumnMasking: "",
        MobileVisible: "true",
        TabsVisible: "true",
        controlFromPageId: selColumn.id,
        src: "page"
      };
      if (props?.data?.["sheetElementIndex"]) {
        newColJson["sheetElementIndex"] = props.data.sheetElementIndex;
        newColJson["sheetIndex"] = parseInt(props.data.sheetIndex);
      }

      if (props?.data?.["sheetFrameElementIndex"]) {
        newColJson["sheetFrameElementIndex"] = props.data.sheetFrameElementIndex;
      }

      if (openCanvasForSubform === "subform") {
        newColJson["isSubForm"] = "true";
      }

      newColJson.id = "1000" + createUniqueId("temp_tableColumn");

      setSelectedColumns((prev) => [...prev, newColJson]);
      setAllColumnsInPage((prev) => [...prev, newColJson]);
      tableColumnsName.push(selColumn.name);
      tableColumnsControlId.push(selColumn.id);
    } else {
      tableColumnsName = tableColumnsName.filter((prev) => prev !== selColumn.name);
      tableColumnsControlId = tableColumnsControlId.filter((prev) => prev !== selColumn.id);
    }
  }

  function handleEscapeKey(e) {
    if (e.code === "Escape" && props?.showColumnListBackdrop === true) {
      props.setShowColumnListBackdrop(false);
    }
  }

  let layout;
  function findLayoutJson(formJson, tableId) {
    if (formJson?.children) {
      for (let child of formJson?.children) {
        if (child?.id == tableId) {
          if (child?.listviewFrameLayoutJson) {
            layout = JSON.stringify(child);
          }
        } else {
          findLayoutJson(child, tableId);
        }
      }
    }
  }

  async function handleClose(arg) {
    findLayoutJson(formState2, props.data.id);
    const remainingURL = "/Properties/addColumn";
    let stepIndex;
    let subStepIndex;
    let subChildMenuIndex;

    let jsonToSend = {};
    const isMenu = formState2?.containerControl === "Menu";
    const isWizard = formState2?.containerControl === "wizard_h" || formState2?.containerControl === "wizard_v";
    const isSideNavigation = formState2?.containerControl === "sidenavigation";

    if (isMenu || isWizard || isSideNavigation) {
      jsonToSend["opType"] = 3;
      stepIndex = props?.data?.stepIndex;
      subStepIndex = props?.data?.subStepIndex;
      subChildMenuIndex = props?.data?.subChildMenuIndex;
    } else {
      jsonToSend["opType"] = 1;
    }

    if (props?.data?.sheetElementIndex || props?.data?.sheetElementIndex === 0) {
      jsonToSend["sheetElementIndex"] = props.data.sheetElementIndex;
      jsonToSend["sheetIndex"] = parseInt(props?.data?.sheetIndex);
      jsonToSend["opType"] = 2;
    }

    if (props?.data?.sheetFrameElementIndex || props?.data?.sheetFrameElementIndex === 0) {
      jsonToSend["sheetFrameElementIndex"] = props.data.sheetFrameElementIndex;
      jsonToSend["opType"] = 4;
    }

    jsonToSend["controlid"] = props?.data?.id;

    if (isMenu) {
      jsonToSend["type"] = "menu";
    }
    if (isWizard || isSideNavigation) {
      jsonToSend["type"] = "wizard";
    }

    if (openCanvasForSubform === "advanceListviewFromScratch") {
      jsonToSend["isAdvanceListview"] = true;
    } else if (openCanvasForSubform === "subform") {
      jsonToSend["isSubForm"] = true;
    }

    jsonToSend["FormId"] = `${
      microFrontendData?.formDefId ||
      activePage?.formId ||
      activeSubForm?.fragmentId ||
      microFrontendData?.processDefId ||
      microFrontendData?.templateId
    }`;
    jsonToSend["layoutJson"] = layout;
    jsonToSend["frameIndex"] = props.data?.frameIndex;
    jsonToSend["elementIndex"] = props.data?.elementIndex;
    jsonToSend["elem_type"] = "column";
    jsonToSend["isModify"] = "false";

    if (jsonToSend["ColumnType"] === "datetimepick") {
      jsonToSend["ColumnType"] = "datetpick";
      jsonToSend["PickerType"] = "2";
    }

    if (arg) {
      for (let column of selectedColumns) {
        if (pageColumnsName.includes(column.controlFromPageId
          )) {
          column.stepIndex = formState2?.isStep?.parentStep || stepIndex;
          let updatedColumn = { ...column, ...jsonToSend };

          await axios.post(remainingURL, updatedColumn, { withCredentials: true }).then((response) => {
            if (response?.data?.status === 0) {
              const colDetails = {};
              colDetails["FormId"] = activePage?.formId || activeSubForm?.fragmentId;
              colDetails["frameIndex"] = props.data.frameIndex;
              colDetails["elementIndex"] = props.data.elementIndex;
              colDetails["elem_type"] = "column";
              colDetails["columnIndex"] = response.data.columnIndex;
              colDetails["elementJson"] = response.data.elementJson;
              column["complexFieldName"] = column.ColumnName;

              colDetails.style = { ...column };
              props.data.columnList.push(colDetails);
            } else if (response.data.status === 1) {
              showAlertConfirmNotifications({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentKey: "Reopen_Form"
              });
            }
          });
        }
      }
    }

    setTimeout(() => {
      updatedComponentInFormState(props.data.id, props.data);
    }, 300);

    props.setShowColumnListBackdrop(false);
    props.setSelectedColumnsFromPage(selectedColumns);
    setIsTableBackdropOpen(false);
  }
  useEffect(() => {
    document.getElementById("backdropDiv")?.focus();
  }, []);
  return (
    <>
      <Backdrop
        style={{ direction: translationStyles1 ? "ltr" : "initial" }}
        id="backdrop"
        className={classes.backdrop}
        open={true}
      >
        <div tabIndex="0" onKeyDown={(e) => handleEscapeKey(e)} id="backdropDiv" className={styles.backdropDiv}>
          <div className={styles.backdropHeaderDiv}>
            <div className={styles.headerLeft}>{t(`${"Select Column"}`)}(s)</div>
            <div className={styles.headerRight}></div>
          </div>

          <div className={styles.backdropBodyDiv}>
            <div className={styles.backdropBodyLeftPart2}>
              <div className={styles.backdropBodyLeftPartColumnListText2}>
                {t(`${"Select the fields you want as Columns"}`)}
              </div>

              {columnDetailsList.map((obj) => {
                return (
                  <>
                    <div className={styles.backdropBodyLeftPartColumnListOddRow2} key={obj.name}>
                      <input
                        type="checkbox"
                        id={obj.name}
                        defaultChecked={selectedColumnsNameFromPage.includes(obj.name)}
                        onClick={(e) => addColumn(e, obj)}
                        key={obj.name}
                        className={styles.selectPageAdvance}
                      />

                      <label
                        aria-label={obj.name}
                        for={obj.name}
                        className={styles.backdropBodyLeftPartColumnListLabel}
                      >
                        {obj.name}
                      </label>

                      <span className={styles.backdropBodyLeftPartColumnListColumnWidth2}>{obj.elem_type}</span>
                    </div>
                  </>
                );
              })}
            </div>

            <div className={styles.backdopBodyRightPart2}>
              <div className={styles.backdropBodyLeftPartColumnListText2}>{t(`${"Selected Columns"}`)}:</div>

              {allColumnsInPage?.map((obj, index) => {
                return pageColumnsName.includes(obj.controlFromPageId
                  ) ? (
                  <div id={index} columnPositionIndex={index}>
                    <div id={index} columnPositionIndex={index} className={styles.backdropBodyRightPartSelectedColumns}>
                      <div className={styles.backdropBodyRightPartSelectedColumnsDiv1} columnPositionIndex={index}>
                        {obj?.ColumnName}
                      </div>

                      <div className={styles.backdropBodyRightPartSelectedColumnsDiv2} columnPositionIndex={index}>
                        {obj.ColumnType}
                      </div>

                      <div className={styles.backdropBodyRightPartSelectedColumnsDiv3} columnPositionIndex={index}>
                        {"10%"}
                      </div>
                    </div>
                  </div>
                ) : null;
              })}

              {columnWidth === "N" ? null : (
                <div id={-1} columnPositionIndex={-1} style={{marginTop: "auto"}}>
                  <div id={-1} columnPositionIndex={-1} className={styles.backdropBodyRightPartSelectedColumns}>
                    <div className={styles.backdropBodyRightPartSelectedColumnsDiv1} columnPositionIndex={-1}>
                      {t("Total column width Left")}
                    </div>

                    <div className={styles.backdropBodyRightPartSelectedColumnsDiv2} columnPositionIndex={-1}>
                      {""}
                    </div>

                    <div className={styles.backdropBodyRightPartSelectedColumnsDiv3} columnPositionIndex={-1}>
                      {100 - columnWidth}%
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>

          <div
            style={{ direction: translationStyles1 ? "ltr" : "initial", paddingLeft: "12px" }}
            id="footer"
            className={styles.colButtonDiv2}
          >
            <Button
              disableFocusRipple
              disableRipple
              tabIndex={0}
              className={`${styles.saveAndExitButton} formBuilder-MuiButton-outlined`}
              onClick={() => handleClose(true)}
            >
              {t(`${"Save and Exit"}`)}
            </Button>
            <Button
              disableFocusRipple
              disableRipple
              tabIndex={0}
              className={`${styles.cancelBackdropButton} formBuilder-MuiButton-outlined`}
              onClick={() => handleClose(false)}
            >
              {t(`${"Cancel"}`)}
            </Button>
          </div>
        </div>
      </Backdrop>
    </>
  );
}
export default AdvanceListViewColumnListBackdrop;
