        props.selectedControl.containerControl === "wizard_v" ||
        props.selectedControl.containerControl === "sidenavigation";

      if (isMenu || isWizard) {
        payload.stepIndex =
          props.selectedControl.stepIndex || props.selectedControl.stepIndex === 0
            ? props.selectedControl.stepIndex
            : "-1";
        payload.subStepIndex =
          props.selectedControl.subStepIndex || props.selectedControl.subStepIndex === 0
            ? props.selectedControl.subStepIndex
            : "-1";
        payload.subChildMenuIndex =
          props.selectedControl.subChildMenuIndex || props.selectedControl.subChildMenuIndex === 0
            ? props.selectedControl.subChildMenuIndex
            : "-1";
      }
      payload.isAdvLVInMenu = isMenu || isWizard;
