  function handleToggle(e, field) {
    const value = e.target.value;
    if (field === "Enable") {
      props.selectedControl.style["previousEnable"] = value;
    }
    if (field === "ReadOnlyStyle") {
      props.selectedControl.style["previousReadOnly"] = value;
    }
    if (field === "ReadOnly") {
      props.selectedControl.style["previousReadOnlyValue"] = value;
    }
    handleProperty(field, value);
  }

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue = event?.key;
      if (propertyName === "CustomId") {
        if(event.target.selectionEnd === event.target.selectionStart){
          passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");
        }
        else {
          let stringArray = propertyValue.split("");
          stringArray.splice(event.target.selectionStart, event.target.selectionEnd - event.target.selectionStart);
          stringArray.splice(event.target.selectionStart, 0, ...event?.key.split(""));
          passValue = stringArray.join("");
        }
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage"
        });
      } else {
        setAlertData({
          open: false,
          showConfirm: "showNotification"
        })
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function handlePropertyText(propertyName, propertyValue) {
    props.selectedControl.style[propertyName] = propertyValue;
    props.applyChanges(props.formData);
  }

  function mappingOpen() {
    setMappingField(true);
  }

  let previousPosition;
  let newPosition;
  const [draggedId, setDraggedId] = useState();
  const [dropId, setDropId] = useState();
  function registerOptionToReposition(event, optionIndex, colName) {
    var draggedIndex;

    for (let i in props.selectedControl.columnList) {
      if (props.selectedControl.columnList[i].style.ColumnName === colName) {
        draggedIndex = i;
      }
    }

    if (optionIndex) previousPosition = parseInt(optionIndex);
    console.log(colName);
    setDraggedId(draggedIndex);
  }

  function allowDrop(event, colId, colName) {
    newPosition = event.target.attributes?.option_index?.value;
    let isParent = event.target.attributes?.option_parent;
    if (isParent) {
      event.target.classList.add("addBorder");
    }
    if (newPosition) newPosition = parseInt(newPosition);
    var draggedIndex;

    for (let i in props.selectedControl.columnList) {
      if (props.selectedControl.columnList[i].style.ColumnName === colName) {
        draggedIndex = i;
      }
    }
    setDropId(draggedIndex);
    event.preventDefault();
  }
  function dragLeave(event) {
    let isParent = event.target.attributes?.option_parent;
    if (isParent) {
      if (event.target.classList.contains("addBorder")) {
        event.target.classList.remove("addBorder");
      }
    }
  }

  function repositionThisOption(event, colId, colName, afterRow, onTop) {
    console.log("here ", colName);
    setDropId(colId);
    if (event.target.attributes?.option_index?.value) {
      newPosition = parseInt(event.target.attributes.option_index.value);
    }
    let isParent = event.target.attributes?.option_parent;
    if (isParent) {
      if (event.target.classList.contains("addBorder")) {
        event.target.classList.remove("addBorder");
      }
    }

    let remainingURL = "";
    var jsonToSend = {};

    if (parentWizardCheck(props?.selectedControl?.id) || parentMenuCheck(props?.selectedControl?.id)) {
      remainingURL = "/Properties/reArrangeColumnInStep";
    } else {
      remainingURL = "/Properties/reArrangeColumn";
    }

    //Menu Control
    if (parentTab(props?.selectedControl?.id)) {
      jsonToSend["sheetIndex"] = props.selectedControl?.sheetIndex;
      jsonToSend["sheetElementIndex"] = props.selectedControl?.sheetElementIndex;
      jsonToSend["sheetFrameElementIndex"] = props.selectedControl?.sheetFrameElementIndex;
    } else if (parentMenuCheck(props?.selectedControl?.id) || parentWizardCheck(props?.selectedControl?.id)) {
      jsonToSend["stepIndex"] = wizard_type_step;
      jsonToSend["subStepIndex"] = wizard_type_subStepIndex ? wizard_type_subStepIndex : "-1";
      jsonToSend["subChildMenuIndex"] = wizard_type_subChildMenuIndex ? wizard_type_subChildMenuIndex : "-1";
    }
    //preapring json to send

    jsonToSend["FormId"] = `${
      microFrontendData?.formDefId ||
      activePage?.formId ||
      activeSubForm?.fragmentId ||
      microFrontendData?.processDefId ||
      microFrontendData?.templateId
    }`;
    jsonToSend["isSubForm"] = openCanvasForSubform === "subform" ? true : false;
    jsonToSend["isAdvanceListview"] = false;
    jsonToSend["frameIndex"] = props.selectedControl?.frameIndex;
    jsonToSend["elementIndex"] = props.selectedControl?.elementIndex;
    jsonToSend["oldIndex"] = draggedId;
    jsonToSend["newIndex"] = dropId;
    jsonToSend["replace"] = afterRow ? "N" : "Y";
    jsonToSend["onTop"] = onTop;

    axios
      .post(remainingURL, jsonToSend, { withCredentials: true })
      .then((response) => {
        if (response.data?.status === "0") {
          if (onTop) {
            const tempControl = props.selectedControl.columnList[draggedId];
            props.selectedControl.columnList.splice(draggedId, 1);
            props.selectedControl.columnList.splice(0, 0, tempControl);
            props.selectedControl.columnList.map((obj, colIndex) => {
              obj.columnIndex = colIndex;
            });
          } else if (afterRow) {
            const tempControl = props.selectedControl.columnList[draggedId];
            props.selectedControl.columnList.splice(draggedId, 1);
            props.selectedControl.columnList.splice(parseInt(dropId) + 1, 0, tempControl);
            props.selectedControl.columnList.map((obj, colIndex) => {
              obj.columnIndex = colIndex;
            });
          } else reArrageColumn(draggedId, dropId);
          updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
        }
      })
      .catch((error) => {
        console.log("Error swapping columns");
      });
    event.preventDefault();
  }

  function reArrageColumn(dragged, drop) {
    var tempColId = props.selectedControl.columnList[dragged].columnIndex;
    props.selectedControl.columnList[dragged].columnIndex = props.selectedControl.columnList[drop].columnIndex;
    props.selectedControl.columnList[drop].columnIndex = tempColId;
    var tempCol = props.selectedControl.columnList[dragged];
    props.selectedControl.columnList[dragged] = props.selectedControl.columnList[drop];
    props.selectedControl.columnList[drop] = tempCol;
  }

